];
export default {
  name: "HelloWorld",
  data() {
    return {
      cityName: "" || "台灣",
      data: [],
      taiwan: taiwan,
      pic: "",
      c3Taiwan:{
        id:null || taiwan[0].name_id,
        cnt:8
      }
    };
  },
  computed: {
    filterName() {
      const vm = this;
      let city = [];
      let cityName = vm.taiwan.filter(item => {
        if (item.name == vm.cityName) {
          vm.pic = item.pic;
          return item;
        }
      });
      city = vm.data.filter(item => {
        if (cityName[0].engname == item.name) {
          return item;
        }
      });
      return city;
    }
  },
  methods: {
    getMap() {
      let vm = this;
      var projection = d3.geo
        .mercator()
        .center([121, 24])
        .scale(8000);
      var path = d3.geo.path().projection(projection);

      d3.json("@/../static/taiwan.json", function(error, topology) {
        console.log(topology);
        console.log(error);
        // 縣市/行政區界線
        d3
          .select("svg")
          .append("path")
          .datum(
            topojson.mesh(
              topology,
              topology.objects["County_MOI_1070516"],
              function(a, b) {
                return a !== b;
              }
            )
          )
          .attr("class", "subunit-boundary");
        d3
          .select("svg")
          .selectAll("path")
          .data(
            topojson.feature(topology, topology.objects.County_MOI_1070516)
              .features
          )
          .enter()
          .append("path")
          .attr({
            d: path,
            name: function(d) {
              return d.properties["COUNTYNAME"];
            },
            fill: "rgba(24,81,18,0.85)",
            stroke: "rgba(255, 255, 255, 0.7)",
            "stroke-width": "2"
          })
          .on("mouseenter", function(d) {
            d3.select(this).attr({
              fill: "rgba(24,81,18,0.55)"
            });
            vm.cityName = d.properties.COUNTYNAME;
          })
          .on("mouseleave", function(d) {
            d3.select(this).attr({
              fill: "rgba(24,81,18,0.85)"
            });
          });
      });
    },
    getData() {
      const vm = this;
      this.axios
        .get(
          "https://api.openweathermap.org/data/2.5/group?id=1668284,6724654,7280290,1675151,1668399,7280289,1674197,1667905,1671971,1671564,1665194,1668352,1670479,1668295,1674502,1670651,1675247,1678836,1679136&lang=zh_TW&units=metric&APPID=fe5145f9c52ca061eec3a0ce448c8864"
        )
        .then(res => {
          console.log(res.data);
          vm.data = [...res.data.list];
          console.log("vm.data", vm.data);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getForecast(){
      console.log('aaa');
      const vm = this;
      let Cid =vm.c3Taiwan.id;
      let cnt =vm.c3Taiwan.cnt;
      this.axios
      .get('http://api.openweathermap.org/data/2.5/forecast?id='+vm.c3Taiwan.id+'&appid=fe5145f9c52ca061eec3a0ce448c8864&cnt='+cnt+'')
      .then(res=>{
         console.log(res.data);
      })
      .catch(err=>{
        console.log(err);
      })
    }
  },
  created() {},
  mounted() {
    this.getData();
    this.getMap();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import url("https://fonts.googleapis.com/css?family=Roboto:500");
main {
  position: relative;
  min-height: 120vh;
  background-image: url("~@/assets/images/bg-kv2.jpg");
  background-position: center;
  background-size: cover;
  z-index: 1;
  &::after {
    content: "";
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 2;
    background: #000;
    right: 0;
    top: 0;
    background: radial-gradient(
      ellipse at center,
      rgba(255, 255, 255, 1) 0%,
      rgba(255, 255, 255, 0) 95%,
      rgba(255, 255, 255, 0) 100%
    );
  }
  .mapInfo {
    letter-spacing: 1.8px;
    position: absolute;
    font-size: 23px;
    h4 {
      color: #2a5cb0;
      font-family: "Roboto", sans-serif;
      font-weight: 500;
      text-shadow: 0 0 0.2em #af95d9;
    }
    
    .weatherInfo {
      font-size: 20px;
      padding-left: 25px;
    }
  }
}
.map {
  background: linear-gradient(
    45deg,
    rgba(255, 241, 235, 0.69) 0%,
    rgba(236, 229, 234, 0.88) 10%,
    rgba(216, 215, 232, 0.89) 21%,
    rgba(205, 219, 242, 0.95) 62%,
    rgba(213, 235, 251, 0.98) 83%,
    rgba(181, 255, 255, 1) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  border-radius: 10px 10px 10px 10px;
  height: 600px;
  margin: 10% auto 0 auto;
  position: relative;
  z-index: 100;
  box-shadow: inset 1px -2px 9px 1px #2a6fb2, -4px 7px 16px 2px #0a2b358c,
    -9px 6px 16px 2px #5ea4e3d9;
  background-size: cover;
}
@media (min-width: 1200px) {
  .map {
    width: 800px;
  }
  .mapInfo {
    right: -25%;
    top: 20%;
  }
}
@media (max-width: 1200px) {
  .map {
    width: 100%;
  }
  .mapInfo {
    right: 50%;
    transform: translateX(50%);
    bottom: -45%;
  }
}
svg {
  position: relative;
  z-index: 99;
  width: 100%;
  height: 100%;
}

#c3-taiwan {
  position: relative;
  z-index: 98;
  padding-top: 50px;
  background: linear-gradient(
      45deg,
      rgba(255, 241, 235, 0.69) 0%,
      rgba(236, 229, 234, 0.88) 10%,
      rgba(216, 215, 232, 0.89) 21%,
      rgba(205, 219, 242, 0.95) 62%,
      rgba(213, 235, 251, 0.98) 83%,
      rgba(181, 255, 255, 1) 100%
  );
  min-height: 100vh;
}

.select {
  position: relative;
  display: block;
  background: #859eb6;
  overflow: hidden;
  border-radius: .25em;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #859eb6;
  background-image: none;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0 0 0 .5em;
  color: #fff;
  cursor: pointer;
}
.select::after {
  content: '\25BC';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 5px 1em;
  background: #859eb6;
  pointer-events: none;
}
/* Transition */
.select:hover::after {
  color: #f39c12;
}
.select::after {
  -webkit-transition: .25s all ease;
  -o-transition: .25s all ease;
  transition: .25s all ease;
}
</style>



